import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import {http} from "@/utils/axios";

const store= new Vuex.Store({
  state: {
    clientinfo:{
   
    },
    logList:[],
    pic:'',
    // props: ["flag"]
  },
  mutations: {
    getClientinfo(state,clientinfo){
      state.clientinfo = {...state.clientinfo,...clientinfo};
    },
    getLoglist(state,logList){
      state.logList=logList 
    },
    getPhoto(state,pic){
      state.pic=pic;
    }
  },
  actions: {
    // 获取用户信息
    getClientinfo({commit},{url}){
      http.get(url).then(res=>{
        commit("getClientinfo",res.data.result)
      })
    },
    
    // 获取日志列表
    getLoglist({commit},{url}){
      http.get(url).then(res=>{
        commit("getLoglist",res.data.result)
      })
    }
    ,  // 获取头像
    getPhoto({commit},{url}){
      http.post(url).then(res=>{
        commit('getPhoto',res.data.result)
      })
    },
  },


  modules: {
  }
})


export default store