<template>
  <el-container style="color:white;">
    <div
      class="logo"
      style="position:absolute;top:0;left:0;line-height:60px;text-indent:30px;"
    >yoya后台管理系统</div>

    <el-header style="text-align: right; font-size: 12px">
      <div class="changeimg">
        <el-tooltip content="点击切换头像" placement="top">
          <div class="pic">
            <img :src="pic" alt srcset @click="postpicture" />
          </div>
        </el-tooltip>
        <input type="file" @change="todoupload" ref="upload" class="fileclass" style="display:none" />
      </div>

      <span></span>
      <span style="margin-right:10px" v-if="token">
        {{clientinfo.username}} |
        <span @click="logout">
          <router-link to="/login" style="color:white">退出登录</router-link>
        </span>
      </span>
      <span style="margin-right:10px" v-else>
        注册 |
        <span>登录</span>
      </span>

      <el-popover placement="top-start" id="prop" width="80" trigger="hover">
        <i class="el-icon-caret-bottom" slot="reference"></i>
        <p style="margin:10px">
          <router-link :to="{name:'clientinfo',query:{
            type:'first'
          }}">修改个人信息</router-link>
        </p>
        <p style="margin:10px">
          <router-link :to="{name:'clientinfo',query:{
            type:'second'
          }}">修改密码</router-link>
        </p>
      </el-popover>
    </el-header>
  </el-container>
</template>

<script>
import Aside from "@/components/Aside.vue";
import pic from "@/assets/d.jpg";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      username: "",
      // pic: "",
      info: {},
      token: sessionStorage.token,
      props: ["flag"]
    };
  },
  computed: {
    ...mapState(["clientinfo"]),
    pic: {
      get() {
        return this.$store.state.pic;
      },
      set(val) {
        this.$store.commit("getPhoto", val);
      }
    }
  },
  mounted() {
    // console.log(this.$store.state);
    // console.log(this.pic);
    var that = this;

    // 请求获取用户名
    if (!this.clientinfo.length > 0) {
      this.getClientinfo({
        url: "/vue/stu/info"
      }).then(res => {});
    }

    // 去数据库里查询图片的数据，
    if (!!(this.pic).length>0) {
      var that=this;
      this.getPhoto(
        { url: "/vue/getavatar" },
        { flag: this.clientinfo.flag }
      ).then(res => {
        // console.log(res);
        that.pic=this.clientinfo.pic;
      });
    } else {
      this.pic = pic;
    }
    // this.$axios
    //   .post("/vue/getavatar", {
    //     flag: this.flag
    //   })
    //   .then(res => {
    //     console.log(res);
    //     if (!!res.data.flag) {
    //       this.pic = res.data.result.pic.replace(
    //         "puplic",
    //         "http://localhost:1909"
    //       );
    //     } else {
    //       this.pic = pic;
    //     }
    //   });
  },
  methods: {
    /* 
    当点击图片的时候，默认触发input的change事件，利用表单特性向后台传递数据，
    input的类型为file
    */
    logout() {
      //  alert(1312)
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("client");
    },
    postpicture() {
      this.$refs.upload.click();
      // console.log(999);
    },


    todoupload() {
      var file = this.$refs.upload.files[0];
      var data = new FormData();
      // console.log(data);
      data.append("avatar", file);
      var that = this;
      
      // if (!this.pic.length > 0) {
      //   this.uploadPic(
      //     { url: "vue/uploadpic" },
      //     { flag: this.clientinfo.flag }
      //   ).then(res => {
      //   //  that.pic = res.data.pic.replace("puplic", "http://localhost:1909");
      //   //  localStorage.avatar = that.pic;
      //   });
      // }

if(!this.pic.length){
        this.$axios({
        url: "vue/uploadpic",
        method: "POST",
        params: {
          flag: this.flag
        },
        data
      }).then(res => {
        // console.log(res);
        this.pic = res.data.pic.replace("puplic", "http://192.168.50.16:1909");
        localStorage.avatar = this.pic;
      });
}

    },
    ...mapMutations(["getClientinfo", "getPhoto"]),
    ...mapActions(["getClientinfo", "getPhoto"])
  },
  components: {
    Aside
  }
};
</script>

<style lang="scss" >
.el-header {
  background-color: #393d49;
  color: #fff;
  line-height: 60px;
}
.el-side {
  height: 90vh;
  background-color: #393d49;
}

.sub {
  display: flex;
  justify-content: space-between;
}
.el-submenu__icon-arrow {
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -7px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  font-size: 12px;
}

.el-icon-arrow-down:before {
  content: "0";
}
.pic {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  right: 120px;
  img {
    width: 100%;
    height: 100%;
  }
}
.router-link-active {
  display: block;

  color: #ffc345;
}
</style>