import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Register from '../views/Register.vue'
import Login from '../views/Login.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: {
      name: "home"
    }
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/clientinfo',
    name: 'clientinfo',
    component: () => import('../views/Clientinfo.vue')
  },
  {
    path: '/goodsmanage',
    name: 'goodsmanage',
    component: () => import('../views/Goodsmanage.vue')
  },
  {
    path: '/report',
    name: 'report',
    component: () => import('../views/Report.vue')
  },
  {
    path: '/chat',
    name: 'chat',
    component: () => import('../views/Chat.vue')
  },

]

const router = new VueRouter({
  routes
})

export default router
