<template>
  <div id="login">
    <el-row id="loginModel">
      <el-col>
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form
            style="color:#409EFF;font-size:24px;    text-align: center; margin-bottom: 28px;
margin-left: 56px;"
          >欢迎登录</el-form>

          <el-form-item label="手机号" prop="mobile">
            <el-input type="mobile" v-model="ruleForm.mobile" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="pass">
            <el-input type="pass" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>

          <p
            style="text-align:right;color:#909399;font-size:12px;margin-bottom:10px;cursor:pointer"
            @click="toRegister()"
          >没有账号，立即注册？</p>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
        var validateMobile = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号"));
      } else {
        if (this.ruleForm.mobile !== "") {
          this.$refs.ruleForm.validateField("checkMobile");
        }
        callback();
      }
    };

    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.pass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };


    return {
      ruleForm: {
        mobile: "",
        pass: ""
      },
      rules: {
        mobile: [{ validator: validateMobile, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // alert("submit!");
          // 请求axios 的数据，实现登录的逻辑
          this.$axios.post("/vue/login", this.ruleForm).then(res => {
            
            // console.log(res.data);  
              this.$router.push({ name:"home"});
              sessionStorage.token = res.data.token;
              // console.log(res.data.result);
              
             sessionStorage.client=JSON.stringify(res.data.result)
          });
        } else {
          // console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //   跳转到的登录页面
    toRegister() {
      this.$router.push({ name: "register" });
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>

body {
  position: relative;

  #loginModel {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid#909399;
    border-radius: 5%;
    padding: 50px 61px 37px 0px;
    background:white;
  }
}
</style>