var obj = {'aa':'11', 'bb':'22', 'cc':'33', 'dd':'44'};
Mock.mock("/api/mock/info",{
    "user|100-800": [{   // 随机生成1到3个数组元素
        'name': '@cname',  // 中文名称
        'id|+1': 88,    // 属性值自动加 1，初始值为88
        'age|18-28': 0,   // 18至28以内随机整数, 0只是用来确定类型
        'birthday': '@date("yyyy-MM-dd")',  // 日期
        'city': '@city(true)',   // 中国城市
        'salenum|10-1000':0,
        'buynum|10-1000':0,
        'color': '@color',  // 16进制颜色
        'isMale|1': true,  // 布尔值
        'isFat|1-2': true,  // true的概率是1/3
        // 'fromObj|2': obj,  // 从obj对象中随机获取2个属性
        // 'fromObj2|1-3': obj,  // 从obj对象中随机获取1至3个属性
        // 'brother|1': ['jack', 'jim'], // 随机选取 1 个元素
        // 'sister|+1': ['jack', 'jim', 'lily'], // array中顺序选取元素作为结果
        // 'friends|2': ['jack', 'jim'] // 重复2次属性值生成一个新数组
    },{
        'gf': '@cname'
    }]
})


Mock.mock("/api/mock/like","get",function(options){
    console.log(options);
    return  Mock.mock({
        "user|1-300": [{
            'name': '@cname',
            'id|+1': 88
        }
      ]
    });
})

var Random = Mock.Random;
Mock.mock("/api/mock/list","post",function(options){
    console.log(options);
    var list = [];
    for(var i = 0;i<200;i++){
        let obj =  {
            uid:Random.id(), 
            title:Random.csentence(5,28),
            city:Random.city(),
            names:Random.cname(),
            pic:Random.image('200x100', '#02adea', 'wuhan1909'),
            time:Random.date('yyyy-MM-dd') + " " + Random.time()
        }
        list.push(obj)
    }

    return {
        code:200,
        msg:'获取mock数据成功',
        result:list
    }
})