<template>
  <div id="register">
    <el-row id="loginModel">
      <el-col>
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form
            style="color:#409EFF;font-size:24px;    text-align: center; margin-bottom: 28px;
margin-left: 56px;"
          >欢迎注册</el-form>

          <el-form-item label="手机号" prop="mobile">
            <el-input type="mobile" v-model="ruleForm.mobile" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="用户名" prop="username">
            <el-input type="username" v-model="ruleForm.username" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>

          <p
            @click="tologin()"
            style="text-align:right;color:#909399;font-size:12px;margin-bottom:10px;cursor:pointer"
          >已有账号，立即登录？</p>
          <p style="text-align:center;color:#909399;font-size:12px;margin:10px 0;"></p>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>
 
<script>
import "@/styles/index.scss";
export default {
  data() {
    var validateMobile = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号"));
      } else {
        if (this.ruleForm.mobile !== "") {
          this.$refs.ruleForm.validateField("checkMobile");
        }
        callback();
      }
    };
    var validateUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入您的名字"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkUsername");
        }
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.checkPass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        mobile: "",
        username: "",
        pass: "",
        checkPass: "",
        // 用v-model来动态判定是老师还是学生
     
      },
      rules: {
        mobile: [{ validator: validateMobile, trigger: "blur" }],
        username: [{ validator: validateUsername, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    // 提交的时候
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log(this.ruleForm);
       
        } else {
          // console.log("error submit!!");
          return false;
        }
      });

      const loading = this.$loading({
        lock: true,
        text: "玩命加载中",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });

      this.$http
        .post("/vue/zhuce", this.ruleForm)
        .then(res => {
          // console.log(res);

          loading.close();
          if (!!res.data.flag) {
            // 用flag来判定是否注册成功，如果是0，就代表失败,并且将手机号以及类型以query的形式带过来
            this.$router.push({
              name: "login",
              query: {
                mobile: this.ruleForm.mobile,
              }
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "success",
              duration: 1000
            });
          }
        });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //   跳转到的登录页面
    tologin() {
      // console.log(111);
      this.$router.push({ name: "login" });
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
#register {
//   background: url(../assets/520.jpg) no-repeat;
  // object-fit: cover;
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
body {
  position: relative;

  #loginModel {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid#909399;
    border-radius: 5%;
    padding: 50px 61px 37px 0px;
    background:white;
  }
}
</style>