import axios from "axios";
import router from "@/router"
var token="";
// 基路径可以将这一部分省略只写一部分，但是不可以和反向代理共存
// axios.defaults.baseURL = 'http://localhost:1909/';
// 在这里配置token一个请求头，以后所有的axios都会携带token
axios.defaults.headers.common['token'] = token;//初始token为空
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


// 在axios里面配置axios
import { Loading,Message } from 'element-ui';



let loadingInstance=null;



//由于axios已经在全局挂起， 在axios  里面导出一个函数，以后要想有提示效果直接调用toast函数就可以了
export function Toast(msg,type,duration){
    type=type||success;
    var duration=duration||800;  Message.success({
        type,
        message:msg,
        center:true,
        duration:800
    })
}
//axios拦截器


// 请求拦截器  ，在发出请求之前做一些业务逻辑
// Add a request interceptor
axios.interceptors.request.use(function (config) {
    // console.log(config);
    // Do something before request is sent

    token = sessionStorage.token ?  sessionStorage.token : "";
    config.headers['token'] = token;
    loadingInstance = Loading.service({
        lock: true,
        text: "玩命加载中",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
    });


    return config;
}, function (error) {
    // Do something with request error
    loadingInstance.close();
    Toast("响应失败",'error')
    return Promise.reject(error);
});




// 响应拦截器，根据响应的状态码 做一些业务逻辑
// Add a response interceptor
axios.interceptors.response.use(function (response) {
    // Do something with response data
    // console.log(response)
    setTimeout(() => {
        loadingInstance.close();
        Message.success({
            message:response.data.msg,
            data:true,
            duration:800,
        })
       if(response.data.code=="10000"){
           router.push({name:"login"})
       } 
    }, 1000);
    
    return response;
}, function (error) {
    // Do something with response error
    loadingInstance.close();
    Toast("响应失败",'error')
    return Promise.reject(error);
});
export const http = axios;