<template>
  <div id="home">
      <Public></Public>
   

    <div class="sub">
   <Aside></Aside>
      <div class="right">
        <Banner></Banner>

        <div>
          <p style="font-size:30px;color:#606266">我的日志</p>
          <el-button
            @click="dialogFormVisible = true"
            type="primary"
            class="addlog"
            style="margin:30px;margin-left:400px"
          >添加日志</el-button>

          <!-- 渲染页面 -->

          <div class="myList">
            <div class="mylog" v-for="(item,i) in logList" style="position:relative" :key="i">
              <div class="title">{{item.title}}</div>
              <div class="time">{{item.value1 | timeFormat('/',":")}}</div>
              <div class="content">{{item.context}}</div>
              <el-button
                type="success"
                style="position:absolute;right:20px;bottom:10px"
                @click="dellog(item._id,i)"
              >删除</el-button>
            </div>
          </div>
        </div>

        <!-- Form -->
        <el-dialog title="我的日志" :visible.sync="dialogFormVisible">
          <el-form :model="form" id="addre">
            <el-form-item label="标题" :label-width="formLabelWidth">
              <el-input v-model="form.title" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="时间" :label-width="formLabelWidth">
              <div class="block">
                <el-date-picker v-model="form.value1" type="datetime" placeholder="选择日期时间"></el-date-picker>
              </div>
            </el-form-item>

            <el-form-item label="内容" :label-width="formLabelWidth">
              <el-input
                type="textarea"
                v-model="form.context"
                placeholder="请输入.."
                style="resize:none;"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialog">确 定</el-button>
          </div>
        </el-dialog>
      </div>
