<script>
// @ is an alias to /src
// import Public from "@/components/Public.vue";
// import Aside from "@/components/Aside.vue";
import Banner from "@/components/Banner.vue";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  name: "home",
  data() {
    return {
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        name: "",
        value1: "",
        context: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""

        // pickerOptions:{}
      },
      value1: "",
      formLabelWidth: "120px"
      // logList: []
    };
  },
  components: {
    // Public,
    // Aside,
    Banner
  },
  computed: {
    logList: {
      get() {
        return this.$store.state.logList;
      },
      set(val) {
        // console.log(val);
        // this.getKeyword(val);
        // 提交一个mutation改变数据
        this.$store.commit("getLoglist", val);
      }
    }
  },
  methods: {
    ...mapMutations(["getLoglist"]),
    ...mapActions(["getLoglist"]),

    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },


    dialog() {
      // console.log(111);
      this.dialogFormVisible = false;
      
// console.log(this.logList)
      // 请求ajax将数据存入到后台
      this.$axios.post("/vue/mylog", this.form).then(res => {
        // 更新
       this.getLoglist({
            url: "/vue/getlog"
          }).then(res => {
            // console.log(res);
          });

      });
    },

    // 点击删除
    dellog(_id, index) {
      console.log(_id); 

      // 请求ajax将数据存入到后台,后端删除
      this.$axios.get("/vue/delmylog", {
          params: {_id:_id}
        }).then(res => {
          // console.log(111);
          // console.log(this);
          this.logList.splice(index,1);
        });
    }

    
  },

  mounted() {
     this.logList="";
    if (!this.logList.length > 0) {
      this.getLoglist({
        url: "/vue/getlog"
      }).then(res => {
       
      });
    }
    // console.log(this.getLoglist);
  },
  created() {
    // 获取后台的数据
       this.logList="";
      if (!this.logList.length > 0) {
        this.getLoglist({
          url: "/vue/getlog"
        }).then(res => {
          // console.log(res);
        });
      }
  
  }
};
</script>
